import React, { useState } from 'react';
import { Button, Form } from 'semantic-ui-react';
import axios from 'axios';




export default function Create() {
    const [code, setCode] = useState('');
    const [position, setPosition] = useState('');
    const [quantity, setQuantity] = useState('');
    const [image, setImage] = useState('');
    const [price, setPrice] = useState('');
    const [description, setDescription] = useState('');

    const postData = () => {
        console.log(code);
        console.log(position);
        console.log(quantity);
        console.log(image);
        console.log(price);
        console.log(description);

        axios.post(`./graphql`, {
            query: `mutation
                {
                    createProduct(input:{
                        code: $code
                        position: $position
                        quantity: $quantity 
                        image: $image
                        price: $price
                        description: $description
                    }){
                        code
                    }
                }`,
            variables: {
                code: code,
                position: position,
                quantity: quantity,
                image: image,
                price: price,
                description: description
            }
            }, {
                headers: {
                    'Content-Type': 'application/json'
                }
            });
    }
    
    return(
        <div>
            <Form className="create-form">
                <Form.Field>
                    <label>Code</label>
                    <input placeholder='HWR16-03' onChange={(e) => setCode(e.target.value)} />
                </Form.Field>
                <Form.Field>
                    <label>Position</label>
                    <input placeholder='#' type="number" onChange={(e) => setPosition(e.target.value)} />
                </Form.Field>
                <Form.Field>
                    <label>Quantity</label>
                    <input placeholder='#' type="number" onChange={(e) => setQuantity(e.target.value)} />
                </Form.Field>
                <Form.Field>
                    <label>Image</label>
                    <input placeholder='Image' type="text" onChange={(e) => setImage(e.target.value)} />
                </Form.Field>
                <Form.Field>
                    <label>Price</label>
                    <input placeholder='0.00' type="number" onChange={(e) => setPrice(e.target.value)} />
                </Form.Field>
                <Form.Field>
                    <label>Description</label>
                    <input placeholder='Description' type="text" onChange={(e) => setDescription(e.target.value)} />
                </Form.Field>
                <Button onClick={postData} type='submit'>Submit</Button>
            </Form>
        </div>
    )
}
